import Vue from 'vue';
import Vuikit from 'vuikit';
import VuikitIcons from '@vuikit/icons';
import Viewer from 'v-viewer';
import '@vuikit/theme';
import 'viewerjs/dist/viewer.css';

import App from './App.vue';

Vue.use(Vuikit);
Vue.use(VuikitIcons);
Vue.use(Viewer);

Vue.config.productionTip = false;

new Vue({
  render: (h) => h(App),
}).$mount('#app');
