<template>
  <section class="timeline">
    <ul>
      <li v-for="(info, index) in data" v-bind:key="index">
        <span></span>
        <div style="font-size: 17px">
          {{ info.school }}
          <span v-if="info.yearStay" class="uk-badge badge-stay">{{
            info.yearStay
          }}</span>
        </div>
        <div style="font-size: 13px" v-html="info.course"></div>
        <div style="margin-top: 0.5em; font-size: 14px">
          {{ info.address }}
        </div>
        <div class="year">
          <span>{{ info.yearEnd }}</span>
          <span>{{ info.yearStart }}</span>
        </div>
      </li>
    </ul>
  </section>
</template>

<script>
export default {
  props: {
    data: [Array, Object],
  },
};
</script>

<style lang="scss" scoped>
$primary: #195f9b;
$light: #fff;
$lightGray: #ccd5db;
$black: #525252;
$blackGray: #404040;

.timeline {
  padding-left: 15px;
}

.timeline ul {
  position: relative;
}

.timeline ul::before {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  height: 100%;
}

.timeline li {
  position: relative;
  margin: 60px 35px;
  width: 90%; // 100%;
  list-style: none;
  line-height: 25px;
}

.timeline li > span {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  left: -25px;
  height: 110%;
  border: 2px solid $lightGray;
  border-radius: none;
  color: $black;
}

.timeline li > span::before,
.timeline li > span::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 8px;
  height: 8px;
  border: 3px solid $light;
  border-radius: 50%;
  left: -7px;
  background: $primary;
}

.timeline li > span::before {
  top: -15px;
}

.timeline li > span::after {
  top: 100%;
}

.timeline li div:nth-child(2) {
  font-size: 1.2em;
  font-weight: bold;
  color: $black;
}

.timeline li div:nth-child(3),
.timeline li div:nth-child(4) {
  font-size: 1em;
  color: $blackGray;
}

.timeline li div:nth-child(3) {
  font-size: 0.9em;
}

.timeline li .year span {
  position: absolute;
  font-size: 1em;
  left: -85px;
  width: 40px;
  text-align: right;
  color: $blackGray;
}

.timeline li .year span:first-child {
  top: -20px;
}

.timeline li .year span:last-child {
  top: 100%;
}

/** Other */
.badge-stay {
  font-size: 10px !important;
  padding: 0 8px;
}
</style>